import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition } from '../../../../../../Bamba.Shared/src/models/field-definition';
import { MetaDataType } from '../../../../../../Bamba.Shared/src/models/metadata-definition';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';
import { UsersDataSource } from '../admin-device/admin-device.component';
import { APIKey } from './apiKey';
import { InterFAXService } from '../../blocks/services/interfax.service';

@Component({
  selector: 'app-api-key',
  templateUrl: './api-key.component.html',
  styleUrls: ['./api-key.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class APIKeyComponent implements OnInit, OnDestroy {
  apiKey: APIKey;
  rightControls: Array<Control> = [];
  fieldName: FieldDefinition;
  fieldUsers: FieldDefinition;

  subscriptions = new Subscription();
  usersDataSource: UsersDataSource;

  constructor(
    private translate: TranslateService,
    private router: Router,
    private hydraApi: HydraApiService,
    private route: ActivatedRoute,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    private toastr: ToastrService,
    private userState: UserStateService,
    private interfaxService: InterFAXService
  ) {}

  ngOnInit(): void {
    this.usersDataSource = new UsersDataSource(this.hydraApi, this.userState, this.interfaxService);

    this.createButtons();
    this.createFields();
    this.getAPIKeyData();
  }

  getAPIKeyData(): void {
    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        if (data.type === 'properties') {
          this.spinner.show(this.translate.instant('Common.Loading'), document.documentElement);
          this.hydraApi.getAPIKey(params.apiKeyId, this.apiKeyLoaded, this.apiKeyLoadFailed);
        }

        if (data.type === 'create') this.apiKeyLoaded(new APIKey());
      })
    );
  }

  apiKeyLoaded = (apiKey: APIKey): void => {
    this.apiKey = apiKey;
    this.spinner.hide();
  };

  apiKeyLoadFailed = (): void => {
    this.toastr.error(this.translate.instant('admin-api-keys.load-failed'));
    this.spinner.hide();
  };

  createFields = (): void => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-api-keys.name-label'),
      placeholder: this.translate.instant('admin-api-keys.name-placeholder'),
      required: true,
    });

    this.fieldUsers = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-api-keys.user-label'),
      placeholder: '',
      required: true,
      datasource: this.usersDataSource,
    });
  };

  createButtons(): void {
    const saveButton = new Control(
      this.onSave,
      this.translate.instant('admin-api-keys.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    const cancelButton = new Control(
      this.onCancel,
      this.translate.instant('admin-api-keys.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.rightControls.push(saveButton);
    this.rightControls.push(cancelButton);
  }

  onSave = (): void => {
    this.spinner.showSavingSpinner();

    if (this.apiKey.id) {
      this.hydraApi.updateAPIKey(this.apiKey, this.apiKeySaved, this.apiKeySaveFailed);
    } else {
      this.hydraApi.createAPIKey(this.apiKey, this.apiKeySaved, this.apiKeySaveFailed);
    }
  };

  onCancel = (): void => {
    this.returnToGrid();
  };

  apiKeySaved = (result: ApiResult): void => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-api-keys.saved-successfully'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-api-keys.validation-errors');
    }

    this.spinner.hide();
  };

  apiKeySaveFailed = (): void => {
    this.toastr.error(this.translate.instant('admin-api-keys.save-failed'));
    this.spinner.hide();
  };

  returnToGrid = (): void => {
    this.router.navigateByUrl(
      'product/settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.APIKeys.Folder'
    );
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
